import React, {useState} from 'react'

import Edit from './Edit'
import {FaRegEdit} from 'react-icons/fa'
import {RiDeleteBin6Line} from 'react-icons/ri'

const Item1 = (props) => {

    const [edit, setEdit] = useState(false);
    const showEdit = () => {
        setEdit(!edit);
    }

    const closeEdit = () => {
        setEdit(false);
    }

    return (
        <div className="category">
            <div className='title'>
                {props.icon1}
                <p>{props.category}</p>
            </div>
            <div className="items1">
                {props.items.map((l) => {
                    return (
                        <div key={l.name}  className="item1">
                            <div className='img-cont'>
                                <div className='img'
                                    style={{content: `url(${l.img})`}}>
                                </div>
                            </div>

                            <div className='text'>
                                <h6>{l.name}</h6>
                                <div className='text-inline'>
                                    {l.icon2}
                                    <p>{l.text}</p>
                                </div>
                            </div>

                            <div className='top-div'>
                                <div className='inline1'>
                                    <h5>{l.name}</h5>
                                    <div className='btns'>
                                        <button type='button' className='edit' onClick={showEdit}>
                                            <FaRegEdit />
                                        </button>
                                        <button type='button' className='delete'>
                                            <RiDeleteBin6Line />
                                        </button>
                                    </div>
                                </div>

                                <button className='launch'>Launch</button>
                            </div>

                            {edit ? <Edit click={closeEdit} url1={l.url} /> : null}
                            {edit ? <div className='dark-bg' onClick={closeEdit}></div> : null}
                        </div>
                    );
                })}
            </div>
        </div>
    )
}

export default Item1
