import React, {useState} from 'react'
import Select from 'react-select';

import {FiShare2, FiStar} from 'react-icons/fi'
import {RiDeleteBin6Line} from 'react-icons/ri'
import { AiOutlineClose } from 'react-icons/ai' 
import '../styles/edit.scss'


const options3 = [
    { value: 'passwords', label: 'Passwords'},
    { value: 'folder2', label: 'Folder2'},
    { value: 'folder3', label: 'Folder3'},
    { value: 'folder4', label: 'Folder4'},
    { value: 'folder5', label: 'Folder5'}
]

const Edit = (props) => {

    const [url, setUrl] = useState('https://www.gmail.com/');
    const [firstname, setFirstname] = useState('Daniel');
    const [email, setEmail] = useState('daniel.griffin@gmail.com');
    const [password, setPassword] = useState('********');
    const [notes, setNotes] = useState('Write notes here');

    return (
        <div className='edit-password-form'>
            <div className='title'>
                <h4>Edit Password</h4>
                <button type='button' onClick={props.click}><AiOutlineClose /></button>
            </div>

            <div className='inputs'>
                <div className='inner-row'>
                    <h6>URL</h6>
                    <input
                        type='url'
                        name='url'
                        value={url}
                        onChange={(e) => setUrl(e.target.value)}
                    />
                </div>
                <div className='inner-row'>
                    <h6>Name</h6>
                    <input
                        type='text'
                        name='firstname'
                        value={firstname}
                        onChange={(e) => setFirstname(e.target.value)}
                    />
                </div>
                <div className='inner-row'>
                    <h6>Email</h6>
                    <input
                        type='email'
                        name='email'
                        value={email}
                        onChange={(e) => setEmail(e.target.value)}
                    />
                </div>
                <div className='inner-row'>
                    <h6>Folder</h6>
                    <Select 
                        options={options3}
                        defaultValue={{value: 'passwords', label: 'Passwords'}}
                    />
                </div>
                <div className='inner-row'>
                    <h6>Site Password</h6>
                    <input
                        type='password'
                        name='password'
                        value={password}
                        onChange={(e) => setPassword(e.target.value)}
                    />
                </div>
                <div className='inner-row'>
                    <h6>Notes</h6>
                    <textarea 
                        value={notes}  
                        onChange={(e) => setNotes(e.target.value)} 
                    />
                </div>

                <div className='inner-row2'>
                    <h6>Advanced Settings</h6>
                    <div className='checkboxes'>
                        <div className='check'>
                            <input type='checkbox' name='check1' id='check1' />
                            <label htmlFor='check1'>Require Master Password Reprompt</label>
                        </div>
                        <div className='check'>
                            <input type='checkbox' name='check2' id='check2' />
                            <label htmlFor='check2'>Autologin</label>
                        </div>
                        <div className='check'>
                            <input type='checkbox' name='check3' id='check3' />
                            <label htmlFor='check3'>Disable Autofill</label>
                        </div>
                    </div>
                </div>
            </div>

            <div className='inner-row3'>
                <div className='btns1'>
                    <button type='button' className='share'>
                        <FiShare2 />
                    </button>
                    <button type='button' className='share'>
                        <FiStar />
                    </button>
                    <button type='button' className='delete'>
                        <RiDeleteBin6Line />
                    </button>
                </div>
                <div className='btns2'>
                    <button type='button' className='cancel'>Cancel</button>
                    <button type='button' className='save'>Save Changes</button>
                </div>
            </div>
        </div>
    )
}

export default Edit
