import React, {useState} from 'react';
import {Link} from 'react-router-dom'
import './toolbar.scss'

import { BiSearch, BiLogOut } from 'react-icons/bi'
import {FaPlusCircle, FaCog, FaComment, FaBell} from 'react-icons/fa'
import {TiArrowSortedDown} from 'react-icons/ti'
import {MdDashboard} from 'react-icons/md'
import { Data } from './data';

import MobileSidebar from '../MobileSidebar/MobileSidebar'

const Toolbar = (props) => {

    const [sort, setSort] = useState('');
    const [search, setSearch] = useState('');

    const [openMenu, setOpenMenu] = useState(false);
    function showMenu(){
        setOpenMenu(!openMenu);
    }

    const [mobileSidebar, setMobileSidebar] = useState(false);
    const showMobileSidebar = () => {
        setMobileSidebar(!mobileSidebar)
    }

    const closeMobileSidebar = () => {
        setMobileSidebar(false)
    }

    return (
        <div className='personal-toolbar'>
            <button type='button' className='dashboard-menu' onClick={showMobileSidebar}>
                <MdDashboard />
            </button>
            <div className='col1'>
                <div className='title'>
                    {props.icon}
                    <p>{props.title}</p>
                </div>

                    <div className='search-container'>
                        <select value={sort} onChange={(e) => setSort(e.target.value)}>
                            <option value="default">Passwords</option>
                            <option value="pass1">Password 1</option>
                            <option value="pass2">Password 2</option>
                        </select>
        
                        <div className='search-bar'>
                            <button type='button'><BiSearch /></button>
                            <input
                                type='text'
                                name='search'
                                value={search}
                                onChange={(e) => setSearch(e.target.value)}
                                placeholder='Search Vault'
                            />
                        </div>

                    </div>
            </div>

            <div className='col2'>
                <div className='buttons'>
                    <button type='button' className='upgrade'>Upgrade</button>
                    <button type='button' className='button'><FaPlusCircle /></button>
                    <button type='button' className='button'><FaCog /></button>
                    <button type='button' className='button'><FaComment /></button>
                    <button type='button' className='button'><FaBell /></button>
                </div>
                <div className='user'>
                    <div className='block'>
                        <h6>Daniel Griffin</h6>
                        <p>daniel.griffin@host.com</p>
                    </div>
                    <div className='inline' onClick={showMenu}>
                        <div className='profile'></div>
                        <TiArrowSortedDown />
                    </div>

                    {openMenu ? <div className='logout-container'>
                        <div className='inline'>
                            <div className='profile2'></div>
                            <div className='block2'>
                                <h6>Daniel Griffin</h6>
                                <p>daniel.griffin@host.com</p>
                                <span>Free User</span>
                            </div>
                        </div>
                        {Data.map((props) => {
                            return(
                                <div className='inline2'>
                                    <Link to={props.to}>
                                        {props.icon} {props.title}
                                    </Link>
                                </div>
                            )
                        })}
                        <button type='button'>
                            <BiLogOut /> Log Out
                        </button>
                    </div> : null}
                </div>
            </div>
            {mobileSidebar ? <MobileSidebar click={closeMobileSidebar} /> : null }
            {mobileSidebar ? <div className='apperance-group-bg' onClick={closeMobileSidebar}></div> : null}
        </div>
    )
}

export default Toolbar
