import { lazy, Suspense, useEffect, useState } from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
} from 'react-router-dom';
import Loading from './components/shared/Loading/Loading';
import ScrollToTop from './ScrollToTop';
import './App.scss';
import Aos from 'aos';
import "aos/dist/aos.css";

import {IntlProvider} from 'react-intl';
import {translate} from './translation/translate';
import {useSelector} from 'react-redux';

import PersonalDashboard from './personal/PersonalDashboard/PersonalDashboard'
import PaymentCards from './personal/PaymentCards/PaymentCards';
import SharingCenter from './personal/SharingCenter/SharingCenter';
import PersonalSecurityDashboard from './personal/PersonalSecurityDashboard/PersonalSecurityDashboard';
import AccountSettings from './personal/AccountSettings/AccountSettings';
import english from './translation/en-GB.json'

const Home = lazy(() => import('./pages/Home/Home'));
const Home2 = lazy(() => import('./pages/Home2/Home2'));
const Pricing = lazy(() => import('./pages/Pricing/Pricing'));
const Contact = lazy(() => import('./pages/Contact/Contact'));
const Apps = lazy(() => import('./pages/Apps/Apps'));
const Login = lazy(() => import('./pages/Login/Login'));
const Signup = lazy(() => import('./pages/Signup/Signup'));
const PasswordVault = lazy(() => import('./pages/PasswordVault/PasswordVault'));
const HowItWorks = lazy(() => import('./pages/HowItWorks/HowItWorks'));
const PasswordManager = lazy(() => import('./pages/PasswordManager/PasswordManager'));
const SaveAutofill = lazy(() => import('./pages/SaveAutofill/SaveAutofill'));
const SecurityDashboard = lazy(() => import('./pages/SecurityDashboard/SecurityDashboard'));
const DarkWebMonitoring = lazy(() => import('./pages/DarkWebMonitoring/DarkWebMonitoring'));
const SharePasswords = lazy(() => import('./pages/SharePasswords/SharePasswords'));
const Blog = lazy(() => import('./pages/Blog/Blog'));
const Biometrics = lazy(() => import('./pages/Biometrics/Biometrics'));
const SSO = lazy(() => import('./pages/SSO/SSO'));
const PasswordGenerator = lazy(() => import('./pages/PasswordGenerator/PasswordGenerator'));
const GoPasswordless = lazy(() => import('./pages/GoPasswordless/GoPasswordless'));
const PasswordSharing = lazy(() => import('./pages/PasswordSharing/PasswordSharing'));
const Premium = lazy(() => import('./pages/Premium/Premium'));
const Families = lazy(() => import('./pages/Families/Families'));
const Teams = lazy(() => import('./pages/Teams/Teams'));
const Business = lazy(() => import('./pages/Business/Business'));
const MFA = lazy(() => import('./pages/MFA/MFA'));
const AdministratorControls = lazy(() => import('./pages/AdministratorControls/AdministratorControls'));
const FederatedLogin = lazy(() => import('./pages/FederatedLogin/FederatedLogin'));


function App() {

  useEffect(() => {
    Aos.init({duration:1350})
  }, [])

    const language = useSelector((state) => state.language.language);


  // const language = useSelector((state) => {
  //   try {
  //     return (`${state.language.language}`);
  //   } 
  //   catch (err) {
  //     return null;
  //   }
  // });

  
  return (
    <Router>
    <Suspense fallback={<Loading />}>

      <IntlProvider
        locale={language}
        formats={{ number: 'en' }}
        messages={translate[language]}
      >

        <ScrollToTop />
          <Switch>
          <Route exact path='/' component={Home} />
          <Route path='/Home2' component={Home2} />
          <Route path='/Pricing' component={Pricing} />
          <Route path='/Contact' component={Contact} />
          <Route path='/Apps' component={Apps} />
          <Route path='/Login' component={Login} />
          <Route path='/Signup' component={Signup} />
          <Route path='/PasswordVault' component={PasswordVault} />
          <Route path='/HowItWorks' component={HowItWorks} />
          <Route path='/PasswordManager' component={PasswordManager} />
          <Route path='/SaveAutofill' component={SaveAutofill} />
          <Route path='/SecurityDashboard' component={SecurityDashboard} />
          <Route path='/DarkWebMonitoring' component={DarkWebMonitoring} />
          <Route path='/SharePasswords' component={SharePasswords} />
          <Route path='/Blog' component={Blog} />
          <Route path='/Biometrics' component={Biometrics} />
          <Route path='/SSO' component={SSO} />
          <Route path='/PasswordGenerator' component={PasswordGenerator} />
          <Route path='/GoPasswordless' component={GoPasswordless} />
          <Route path='/PasswordSharing' component={PasswordSharing} />
          <Route path='/Premium' component={Premium} />
          <Route path='/Families' component={Families} />
          <Route path='/Teams' component={Teams} />
          <Route path='/Business' component={Business} />
          <Route path='/MFA' component={MFA} />
          <Route path='/AdministratorControls' component={AdministratorControls} />
          <Route path='/FederatedLogin' component={FederatedLogin} />
          <Route path='/PersonalDashboard' component={PersonalDashboard} />
          <Route path='/PaymentCards' component={PaymentCards} />
          <Route path='/SharingCenter' component={SharingCenter} />
          <Route path='/PersonalSecurityDashboard' component={PersonalSecurityDashboard} />
          <Route path='/AccountSettings' component={AccountSettings} />


          <Redirect to='/' />
        </Switch>
      </IntlProvider>
    </Suspense>
  </Router>
  );
}

export default App;

