import React, { useState, useEffect } from "react";
import "./useThemeSwitcher.scss";

import {BsMoon} from 'react-icons/bs'

function useThemeSwitcher() {
  const [mode, setMode] = useState(() => localStorage.getItem("mode"));

  useEffect(() => {
    window.addEventListener("storage", setPreferedTheme);
    return () => {
      window.removeEventListener("storage", setPreferedTheme);
    };
  }, []);

  const setPreferedTheme = () => {
    const _mode = localStorage.getItem("mode");
    if (_mode) {
      setMode(_mode);
    } else {
      setMode("light");
    }
  };

  useEffect(() => {
    if (mode === "dark") {
      document.body.classList.add("dark-mode");
      localStorage.setItem("mode", "dark");
    } else {
      document.body.classList.remove("dark-mode");
      localStorage.setItem("mode", "light");
    }
  }, [mode]);

  return (
        <label className="switch-mode">
          <input
            type="checkbox"
            onChange={() =>
              setMode((mode) => (mode === "dark" ? "light" : "dark"))
            }
          />
          <div className='dark-icon'><BsMoon /></div>
        </label>
  );
}

export default useThemeSwitcher;