import React, {useState} from 'react'
import './Appearance.scss'

import apperance1 from '../../assets/shared/apperance1.png'
import apperance2 from '../../assets/shared/apperance2.png'
import apperance3 from '../../assets/shared/apperance3.png'
import apperance4 from '../../assets/shared/apperance4.png'
import apperance5 from '../../assets/shared/apperance5.png'
import apperance6 from '../../assets/shared/apperance6.png'

const Data = [
    {
        category:'HostPassword choice',
        options:[
            {
                img:apperance1,
                name:'Classic'
            },
            {
                img:apperance2,
                name:'Dark'
            },
            {
                img:apperance3,
                name:'Minimal'
            }
        ]
    },
    {
        category:'Colors',
        options:[
            {
                img:apperance4,
                name:'Terminal'
            },
            {
                img:apperance5,
                name:'Red'
            },
            {
                img:apperance6,
                name:'Purple'
            }
        ]
    }
]

const Appearance = () => {

    const RadioInput = ({label, value, checked, setter, img}) => {
        return (
          <label className='option'>
            <div className='inline'>
                <input type="radio" checked={checked == value}
                    onChange={() => setter(value)} />
                <div className='name'>{label}</div>
            </div>
            <div className='img'
                style={{content:`url(${img})`}}>
            </div>
          </label>
        );
    };

    const [appearance, setAppearance] = useState();

    return (
        <div className='appearance-group'>
            <h1>Appearance</h1>
            <p className='p'>
                Change the appearance of HostPassword across all of the platform.
            </p>

            <div className='options-container'>
                {Data.map((props) => {
                    return(
                        <div className='category'>
                            <span className='category-title'>{props.category}</span>
                            <div className='options'>
                                {props.options.map((l) =>{
                                    return(
                                        <RadioInput 
                                            label={l.name} 
                                            value={l.name} 
                                            img={l.img}
                                            checked={appearance} 
                                            setter={setAppearance} 
                                        />
                                    )
                                })}
                            </div>
                        </div>
                    )
                })}
            </div>

            <div className='btns'>
                <button type='button' className='btn1'>
                    Create a custom theme
                </button>
                <button type='button' className='btn2'>
                    Create theme
                </button>
            </div>
        </div>
    )
}

export default Appearance
