import React, {useState} from 'react'
import {Link} from 'react-router-dom'

import { Data1, Data2, Data3, Data4, Data5 } from './data'
import {ToggleView} from './ThemeSwitcher/ToggleView'
import Appearance from './Apperance/Appearance'

import {FaCog, FaPlusCircle} from 'react-icons/fa'
import {BiSearch} from 'react-icons/bi'
import {HiUsers} from 'react-icons/hi'
import {AiFillPlusCircle, AiOutlineMenu} from 'react-icons/ai'
import {GoKebabVertical} from 'react-icons/go'
import {MdKeyboardArrowLeft} from 'react-icons/md'
import {BsFillQuestionCircleFill} from 'react-icons/bs'

import './style.scss'
import './darkmode.scss'

const Sidebar = () => {

    const [search, setSearch] = useState('');

    const [sidebar, setSidebar] = useState(false);
    const changeSidebar = () => {
        setSidebar(!sidebar);
    }

    const [openAppearance, setOpenAppearance] = useState(false);
    function showAppearance(){
        setOpenAppearance(!openAppearance);
    }

    function closeAppearance(){
        setOpenAppearance(false);
    }

    
    return (
        <div className={sidebar ? 'personal-sidebar small-personal-sidebar' : 'personal-sidebar'}>
            <div className='logo-container'>
               <Link to='/'>
                    <div className='logo'></div>
                    <h6>HostPassword</h6>
                </Link>
                <button type='button' onClick={changeSidebar}>{sidebar ? <AiOutlineMenu /> : <MdKeyboardArrowLeft />}</button>
            </div> 

            {Data1.map((props) => {
                return(
                    <div className='link-cont'>
                        <Link to={props.to}>
                            {props.icon} <span>{props.link}</span>
                        </Link>
                    </div>
                )
            })}

            <div className='categories'>
                <p>CATEGORIES</p>
                {Data2.map((props) => {
                    return(
                        <div className='link-cont'>
                            <Link to={props.to}>
                                {props.icon} <span>{props.link}</span>
                            </Link>
                        </div>
                    )
                })}
                <button type='button' className='add'>
                    <AiFillPlusCircle /> <span>Add new category</span>
                </button>
            </div>

            <div className='teams'>
                <div className='inline'>
                    <p>TEAMS</p>
                    <div className='buttons'>
                        <button type='button'><FaCog /></button>
                        <button type='button' ><FaPlusCircle /></button>
                    </div>
                </div>

                <div className='search-bar'>
                    <button type='button'><BiSearch /></button>
                    <input
                        type='text'
                        name='search'
                        value={search}
                        onChange={(e) => setSearch(e.target.value)}
                        placeholder='Find team'
                    />
                </div>

                {Data4.map((props) => {
                    return(
                        <div className='team-item'>
                            <div className='icon'>
                                <HiUsers className='users'></HiUsers>
                                <div className='circle'></div>
                            </div>
                            <div className='block'>
                                <span>{props.name} Team</span>
                                <small>{props.members} members</small>
                            </div>
                            <button type='button' className='menu'><GoKebabVertical /></button>
                        </div>
                    )
                })}
                <button type='button' className='all'>View All</button>
            </div>

            <div className='premium'>
                <div className='img'></div>
                <h5>Upgrade to Premium</h5>

                {Data5.map((props) => {
                    return(
                        <div className='item'>
                            {props.icon} <span>{props.description}</span>
                        </div>
                    )
                })}

                <button type='button'>Upgrade</button>
            </div>

            {Data3.map((props) => {
                return(
                    <div className='link-cont'>
                        <Link to={props.to}>
                            {props.icon} <span>{props.link}</span>
                        </Link>
                    </div>
                )
            })}

            <div className='appearance-row'>
                <button type='button'>Advanced Options</button>
                <button type='button' onClick={showAppearance}>Appearance</button>
            </div>

            <div className='mode-container'>
                <ToggleView />
                <div className='help'>
                    <Link to='/'><BsFillQuestionCircleFill /> Help</Link>
                </div>
            </div>

            {openAppearance ? <Appearance /> : null}
            {openAppearance ? <div className='apperance-group-bg' onClick={closeAppearance}></div> : null}
        </div>
    )
}

export default Sidebar
